
import WebStorageService from '@/services/web-storage.service'

const webStorageService = new WebStorageService()

const state = {
  settings: {
    difficulty: 0
  }
}

// Getter functions
const getters = {
  getSettings () {
    return JSON.parse(JSON.stringify(state.settings))
  },
  getDifficulty () {
    return JSON.parse(JSON.stringify(state.settings.difficulty))
  }
}

// Mutations
const mutations = {
  setSetting (state, data) {
    state.settings = { ...state.settings, ...data }
    webStorageService.setLocalStorage('settings', state.settings)
  },
  setSettings (state, data) {
    state.settings = data
    webStorageService.setLocalStorage('settings', state.settings)
  }
}

// Actions
const actions = {
  changeDifficulty ({ commit }, data) {
    commit('setSetting', { difficulty: data })
  },
  changeSettings ({ commit }, data) {
    commit('setSettings', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
