import Vue from 'vue'
import Vuex from 'vuex'
import game from './modules/game'
import settings from './modules/settings'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    i18n: null
  },
  mutations: {
    setLang (state, lang) {
      state.i18n = lang
    }
  },
  actions: {
    changeLang ({ commit }, data) {
      commit('setLang', data)
    }
  },
  modules: {
    game,
    settings
  }
})
