<template>
  <div class="menu">
    <v-speed-dial
      v-model="settingsMenu"
      :top="true"
      :right="true"
      direction="left"
      transition="slide-x-reverse-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="settingsMenu"
          :color="
            settings.difficulty
              ? settings.difficulty === 2
                ? 'deep-purple'
                : 'purple'
              : 'blue'
          "
          dark
          fab
        >
          <div
            class="menu_lang"
            :class="['menu_lang menu_lang-' + i18n.lang]"
          ></div>
          <v-icon v-if="settingsMenu"> mdi-close </v-icon>
          <v-icon v-else> mdi-cog </v-icon>
        </v-btn>
      </template>

      <v-btn
        fab
        dark
        small
        color="blue"
        :class="{ 'v-btn--menu--selected': i18n.lang === 'ja' }"
        @click="changeLang('ja')"
        >JP</v-btn
      >
      <v-btn
        fab
        dark
        small
        color="green"
        :class="{ 'v-btn--menu--selected': i18n.lang === 'vi' }"
        @click="changeLang('vi')"
        >VI</v-btn
      >
      <v-btn
        fab
        dark
        small
        color="red"
        :class="{ 'v-btn--menu--selected': i18n.lang === 'en' }"
        @click="changeLang('en')"
        >EN</v-btn
      >
    </v-speed-dial>
    <div
      :class="[
        { 'v-speed-dial--is-active': settingsMenu },
        'menu_placeholder v-speed-dial v-speed-dial--top v-speed-dial--right v-speed-dial--direction-bottom v-speed-dial--direction-diagonal',
      ]"
    >
      <div class="v-speed-dial__list">
        <div>
          <v-btn
            fab
            dark
            small
            color="blue"
            :class="{ 'v-btn--menu--selected': settings.difficulty === 0 }"
            @click="setDifficulty(0)"
          >
            <v-icon>mdi-baby-bottle</v-icon>
          </v-btn>
        </div>
        <div>
          <v-btn
            fab
            dark
            small
            color="purple"
            :class="{ 'v-btn--menu--selected': settings.difficulty === 1 }"
            @click="setDifficulty(1)"
          >
            <v-icon>mdi-chess-king</v-icon>
          </v-btn>
        </div>
        <div>
          <v-btn
            fab
            dark
            small
            color="deep-purple"
            :class="{ 'v-btn--menu--selected': settings.difficulty === 2 }"
            @click="setDifficulty(2)"
          >
            <v-icon>mdi-skull</v-icon>
          </v-btn>
        </div>
      </div>
    </div>
    <div
      :class="[
        { 'v-speed-dial--is-active': settingsMenu },
        'menu_placeholder v-speed-dial v-speed-dial--top v-speed-dial--right v-speed-dial--direction-bottom',
      ]"
    >
      <div class="v-speed-dial__list">
        <div>
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn fab dark small color="blue" v-bind="attrs" v-on="on"
                ><v-icon>mdi-help</v-icon></v-btn
              >
            </template>
            <v-card>
              <v-card-title class="v-card__title headline">{{
                game.hint > (settings.difficulty + 2) * 2
                  ? i18n.solution
                  : i18n.hint
              }}</v-card-title>
              <v-card-text
                :class="[
                  'text-center hint_content hint_content-' +
                    settings.difficulty,
                ]"
              >
                <template v-for="(sol, i) in game.solution">
                  <template v-if="i * 2 <= game.hint">
                    <h2 :key="i">
                      <span class="hint_number">{{ sol.n1Val }}</span>
                      <span class="hint_operator">{{ sol.op }}</span>
                      <span class="hint_number">{{
                        game.hint === i * 2 ? "?" : sol.n2Val
                      }}</span>
                      <span class="hint_operator">=</span>
                      <span class="hint_number">{{
                        game.hint === i * 2 ? "?" : sol.res
                      }}</span>
                    </h2>
                  </template>
                </template>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn text color="grey" @click="dialog = false">{{
                  i18n.close
                }}</v-btn>
                <v-btn depressed color="teal white--text" @click="nextHint">
                  <template v-if="(settings.difficulty + 2) * 2 < game.hint">{{
                    i18n.done
                  }}</template>
                  <template v-else
                    >{{ i18n.more }}{{ game.hint > 0 ? i18n.exclamation : ""
                    }}{{ game.hint > 1 ? i18n.exclamation : "" }}</template
                  >
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </div>
    </div>
    <v-overlay :value="settingsMenu"></v-overlay>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import CommonService from '@/services/common.service'

export default {
  name: 'Game',
  computed: mapState({
    settings: (state) => state.settings.settings,
    game: (state) => state.game,
    i18n: (state) => state.i18n
  }),
  mounted () {},
  data: () => ({
    settingsMenu: false,
    dialog: false,
    commonService: new CommonService()
  }),
  methods: {
    setDifficulty (val) {
      this.changeDifficulty(val)
    },
    changeLang (val) {
      this.commonService.changeLang(val)
    },
    nextHint () {
      if ((this.settings.difficulty + 2) * 2 < this.game.hint) {
        this.dialog = false
        return
      }
      this.changeHint(this.game.hint + 1)
    },
    ...mapActions('settings', ['changeDifficulty']),
    ...mapActions('game', ['changeHint'])
  }
}
</script>

<style lang="scss">
.menu {
  .v-speed-dial {
    position: absolute;
    z-index: 6;
  }
  &_placeholder {
    pointer-events: none;
    width: 56px;
    margin-top: 56px;
    .v-speed-dial__list {
      opacity: 0;
      transition: opacity 300ms cubic-bezier(0.25, 0.8, 0.5, 1),
        transform 300ms cubic-bezier(0.25, 0.8, 0.5, 1);
      transform: translateY(-15px);
      > * {
        transition: transform cubic-bezier(0.25, 0.8, 0.5, 1);
        &:nth-child(1) {
          transition-delay: 100ms;
          transition-duration: 200ms;
          transform: translateY(-1px);
        }
        &:nth-child(2) {
          transition-delay: 100ms;
          transition-duration: 100ms;
          transform: translateY(-0.5px);
        }
      }
    }
    &.v-speed-dial--is-active {
      pointer-events: all;
      .v-speed-dial__list {
        opacity: 1;
        transform: translateY(0);
        transition: opacity 250ms cubic-bezier(0.25, 0.8, 0.5, 1),
          transform 250ms cubic-bezier(0.25, 0.8, 0.5, 1);
        > * {
          transition: transform cubic-bezier(0.25, 0.8, 0.5, 1);
          &:nth-child(1) {
            transition-delay: 100ms;
            transition-duration: 200ms;
            transform: translateY(0);
          }
          &:nth-child(2) {
            transition-delay: 200ms;
            transition-duration: 100ms;
            transform: translateY(0);
          }
        }
      }
    }
    &.v-speed-dial--direction-diagonal {
      transform: rotate(45deg);
      transform-origin: 28px -28px;
      .v-btn {
        transform: rotate(-45deg);
      }
    }
  }
  .v-btn--menu--selected {
    border: thin solid #fff;
    border-color: #fff !important;
  }
}
.hint {
  &_content {
    &-0 {
      height: 86px;
    }
    &-1 {
      height: 108px;
    }
    &-2 {
      height: 130px;
    }
  }
  &_number {
    display: inline-block;
    min-width: 50px;
    text-align: center;
  }
  &_operator {
    display: inline-block;
    min-width: 25px;
    text-align: center;
  }
}
</style>
