
const state = {
  hint: 0,
  numbers: [],
  solution: []
}

// Getter functions
const getters = {
  getHint () {
    return JSON.parse(JSON.stringify(state.hint))
  },
  getNumbers () {
    return JSON.parse(JSON.stringify(state.solution))
  },
  getSolution () {
    return JSON.parse(JSON.stringify(state.solution))
  }
}

// Mutations
const mutations = {
  setHint (state, data) {
    state.hint = data
  },
  setNumbers (state, data) {
    state.numbers = data
  },
  setSolution (state, data) {
    state.solution = data
  }
}

// Actions
const actions = {
  changeHint ({ commit }, data) {
    commit('setHint', data)
  },
  changeNumbers ({ commit }, data) {
    commit('setNumbers', data)
  },
  changeSolution ({ commit }, data) {
    commit('setSolution', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
