<template>
  <div class="game pa-4" ref="game">
    <h1 class="text-center teal--text">{{ i18n.title }}</h1>
    <div class="game_goal text-center">
      <v-chip color="teal white--text" large class="ma-2">{{ goal }}</v-chip>
    </div>
    <Fireworks v-if="win" />
    <v-card class="calculate mt-2">
      <div
        v-if="!operatorHistory.length && isNaN(selectedNumbers[0])"
        class="text-center grey--text pa-2"
      >
        <h2>
          {{ i18n.combine }}
          <v-icon color="grey" class="calculate_arrow" large>
            mdi-arrow-down-thick </v-icon
          ><br />
          {{ i18n.reach }}
          <v-chip color="teal white--text" class="ma-2">{{ goal }}</v-chip>
        </h2>
      </div>
      <div class="calculate_content_container pa-3">
        <div class="calculate_content">
          <div
            v-for="(history, i) in operatorHistory"
            class="calculate_content_row"
            :key="'h' + i"
          >
            <v-chip depressed color="purple" class="white--text ma-1">{{
              numbers[numberHistory[i * 2]]
            }}</v-chip>
            <v-chip depressed color="deep-purple" class="white--text ma-1">{{
              operators[history]
            }}</v-chip>
            <v-chip depressed color="purple" class="white--text ma-1">{{
              numbers[numberHistory[i * 2 + 1]]
            }}</v-chip>
            <span class="ma-1">=</span>
            <v-chip
              depressed
              :color="numbers[6 + i] === goal ? 'green' : 'indigo'"
              class="white--text ma-1"
              >{{ numbers[6 + i] }}</v-chip
            >
            <div class="calculate_content_last">
              <v-btn
                v-if="!win"
                depressed
                fab
                x-small
                @click="resetCalculate(i)"
              >
                <v-icon dark> mdi-close </v-icon></v-btn
              >
            </div>
          </div>
          <div v-if="!isNaN(selectedNumbers[0])" class="calculate_content_row">
            <v-chip depressed color="purple" class="white--text ma-1">{{
              numbers[selectedNumbers[0]]
            }}</v-chip>
            <v-chip
              v-if="operators[operator]"
              depressed
              color="deep-purple"
              class="white--text ma-1"
              >{{ operators[operator] }}</v-chip
            >
            <div class="calculate_content_last">
              <v-btn depressed fab x-small @click="resetCalculate">
                <v-icon dark> mdi-close </v-icon></v-btn
              >
            </div>
          </div>
        </div>
      </div>
    </v-card>
    <v-card class="choose mt-5 pa-2">
      <div
        class="choose_numbers"
        :class="{ 'choose_numbers-long': settings.difficulty > 0 }"
      >
        <v-btn
          v-for="(number, i) in numbers"
          :color="i > 5 ? 'amber' : 'blue'"
          :key="'n' + i"
          :class="[{ 'white--text': i < 6 }, 'ma-2']"
          large
          @click="selectNumber(i)"
          :disabled="checkValid(i)"
          >{{ number }}</v-btn
        >
      </div>
      <div class="choose_operators mt-1">
        <v-btn
          v-for="(op, i) in operators"
          fab
          color="deep-purple"
          class="white--text ma-2 choose_operator"
          :key="'o' + i"
          @click="selectOperator(i)"
        >
          <v-icon v-if="op === '÷'">mdi-division</v-icon>
          <v-icon v-if="op === 'x'">mdi-close</v-icon>
          <v-icon v-if="op === '-'">mdi-minus</v-icon>
          <v-icon v-if="op === '+'">mdi-plus</v-icon>
        </v-btn>
      </div>
    </v-card>
    <div class="text-center mt-4">
      <v-btn
        :color="win ? 'green' : 'teal'"
        class="ma-2 white--text"
        large
        @click="newGame"
        >{{ win ? i18n.more : i18n.new }}</v-btn
      >
      <v-btn
        v-if="!win"
        class="ma-2 grey--text"
        text
        large
        @click="resetCalculate(0)"
        >{{ i18n.reset }}</v-btn
      >
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '../services/game.service'
import Fireworks from '@/components/Fireworks.vue'

export default {
  name: 'Game',
  components: {
    Fireworks
  },
  mounted () {
    this.newGame()
  },
  computed: mapState({
    settings: (state) => state.settings.settings,
    game: (state) => state.game,
    i18n: (state) => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    operators: ['÷', 'x', '-', '+'],
    numbers: [],
    selectedNumbers: [],
    numberHistory: [],
    operatorHistory: [],
    operator: undefined,
    goal: 0,
    win: false
  }),
  methods: {
    selectNumber (i) {
      if (this.win) {
        return
      }
      if (isNaN(this.operator)) {
        this.$set(this.selectedNumbers, 0, i)
      } else {
        this.numberHistory.push(this.selectedNumbers[0], i)
        this.operatorHistory.push(this.operator)
        const newNumber = this.calculate(i)
        this.numbers.push(this.calculate(i))
        this.resetCalculate()
        if (newNumber === this.goal) {
          this.win = true
        } else {
          this.$set(this.selectedNumbers, 0, this.numbers.length - 1)
        }
      }
    },
    selectOperator (op) {
      if (isNaN(this.selectedNumbers[0])) {
        if (this.numberHistory.length) {
          this.$set(this.selectedNumbers, 0, this.numbers.length - 1)
        } else {
          return
        }
      }
      this.operator = op
    },
    calculate (i) {
      this.$set(this.selectedNumbers, 1, i)
      return this.gameService.getOperator(
        this.numbers[this.selectedNumbers[0]],
        this.numbers[this.selectedNumbers[1]],
        this.operators[this.operator]
      )
    },
    checkValid (i) {
      const n1 = this.numbers[this.selectedNumbers[0]]
      const n2 = this.numbers[i]
      console.log()
      if (
        this.numberHistory.includes(i) ||
        (this.operator === 2 && n2 >= n1) ||
        (this.operator === 0 &&
          (n1 / n2 - Math.floor(n1 / n2) || n1 === n2 || n2 === 1))
      ) {
        return true
      }
    },
    resetCalculate (i) {
      this.selectedNumbers = []
      this.operator = undefined
      if (!isNaN(i)) {
        const toRemove = this.operatorHistory.length - i
        this.operatorHistory.splice(i, toRemove)
        this.numberHistory.splice(i * 2, toRemove * 2)
        this.numbers.splice(i + 6, toRemove)
      }
    },
    newGame () {
      const data = this.gameService.startGame()
      this.resetCalculate(0)
      this.numbers = this.game.numbers
      this.goal = data.goal
      this.win = false
      this.changeHint(0)
    },
    ...mapActions('game', ['changeHint'])
  },
  watch: {
    settings () {
      this.newGame()
    }
  }
}
</script>

<style lang="scss">
@keyframes arrow {
  0% {
    transform: scale(1, 1) translateY(-3px);
  }
  40% {
    transform: scale(1.05, 1.05) translateY(3px);
  }
  100% {
    transform: scale(1, 1) translateY(-3px);
  }
}

html,
body {
  background-color: #f5f5f5;
}

.theme--light.v-application {
  background-color: transparent;
}

.game {
  height: 100vh;
  display: flex;
  flex-direction: column;
  transition: height 0.5s cubic-bezier(0.25, 0.8, 0.5, 1);
  max-width: 612px;
  margin: 0 auto;

  .v-chip {
    padding: 0;
    &.v-size--large {
      min-width: 54px;
      font-size: 24px;
      justify-content: center;
    }
    &.v-size--default {
      min-width: 32px;
      justify-content: center;
    }
  }
}

.choose {
  &_operators {
    display: flex;
  }
  &_operator {
    .v-icon {
      &::before {
        font-weight: bold;
      }
    }
  }
  .v-btn.v-size--large {
    font-size: 1rem;
  }
  .v-btn.v-size--default {
    font-size: 1rem;
  }
}

.calculate {
  flex: 1;
  position: relative;
  max-height: 224px;
  &.v-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &_content {
    width: 100%;
    &_container {
      position: absolute;
      overflow: auto;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      display: flex;
      flex-direction: column-reverse;
    }
    &_row {
      display: flex;
      align-items: center;
    }
    &_last {
      flex: 1;
      text-align: right;
    }
  }
  &_arrow {
    animation: arrow 2s infinite;
  }
}

@media screen and (max-width: 414px) {
  html,
  body {
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    transition: height 0.5s cubic-bezier(0.25, 0.8, 0.5, 1);
  }
  .game {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    height: auto;
    height: calc(var(--vh, 1vh) * 100);
  }

  .choose {
    &_numbers {
      &-long {
        min-height: 180px;
      }
    }
    &_operators {
      justify-content: flex-end;
    }
  }

  body {
    .v-btn:not(.v-btn--round).v-size--large {
      padding: 0 5px;
      min-width: calc(25% - 16px);
    }
  }
}

@media screen and (max-width: 359px) {
  .game {
    &_goal {
      position: absolute;
      left: 9px;
      top: 9px;
      width: 70px;
    }
  }
  .calculate {
    &.mt-2 {
      margin-top: 16px !important;
    }
  }
  .choose {
    &_numbers {
      min-height: 120px;
    }
    &_operators {
      .v-btn--fab.v-size--default {
        height: 44px;
        width: 44px;
      }
    }
  }
}
</style>
