<template>
  <div class="pyro">
    <div class="before"></div>
    <div class="after"></div>
  </div>
</template>

<script>
export default {
  name: 'Fireworks'
}
</script>

<style lang="scss">
$particles: 50;
$width: 500;
$height: 500;

// Create the explosion...
$box-shadow: ();
$box-shadow2: ();
@for $i from 0 through $particles {
  $box-shadow: $box-shadow,
    random($width)-$width /
      2 +
      px
      random($height)-$height /
      1.2 +
      px
      hsl(random(360), 100, 50);
  $box-shadow2: $box-shadow2, 0 0 #fff;
}
@mixin keyframes($animationName) {
  @keyframes #{$animationName} {
    @content;
  }
}

@mixin animation-delay($settings) {
  animation-delay: $settings;
}

@mixin animation-duration($settings) {
  animation-duration: $settings;
}

@mixin animation($settings) {
  animation: $settings;
}

@mixin transform($settings) {
  transform: $settings;
}

.pyro {
  z-index: 1;
}

.pyro > .before,
.pyro > .after {
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  box-shadow: $box-shadow2;
  @include animation(
    (
      1s bang ease-out 3 backwards,
      1s gravity ease-in 3 backwards,
      1s opacity ease-in 3 backwards,
      5s position linear 3 backwards
    )
  );
}

.pyro > .after {
  @include animation-delay((1.25s, 1.25s, 1.25s, 1.25s));
  @include animation-duration((1.25s, 1.25s, 1.25s, 6.25s));
}

@include keyframes(bang) {
  to {
    box-shadow: $box-shadow;
  }
}

@include keyframes(gravity) {
  to {
    @include transform(translateY(200px));
  }
}

@include keyframes(opacity) {
  0% {
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@include keyframes(position) {
  0%,
  19.99% {
    margin-top: 10%;
    margin-left: 30%;
  }
  20%,
  39.99% {
    margin-top: 40%;
    margin-left: 10%;
  }
  40%,
  59.99% {
    margin-top: 20%;
    margin-left: 50%;
  }
  60%,
  79.99% {
    margin-top: 30%;
    margin-left: 0%;
  }
  80%,
  99.99% {
    margin-top: 30%;
    margin-left: 60%;
  }
}
</style>
